@tailwind base;
@tailwind components;
@tailwind utilities;

.sidebar-btn {
    @apply disabled:opacity-60 border-2 leading-none p-2 rounded-full bg-transparent hover:bg-slate-100 text-slate-500 hover:text-blue-500 border-slate-200 hover:border-blue-500 dark:bg-transparent dark:hover:bg-slate-900 dark:text-slate-500 dark:hover:text-emerald-500 dark:border-slate-700 dark:hover:border-emerald-400;
}

.tool-btn {
    @apply disabled:opacity-60 border-2 border-none leading-none p-1 rounded-full bg-transparent hover:bg-slate-100 text-slate-500 hover:text-blue-500 dark:bg-transparent dark:hover:bg-slate-900 dark:text-slate-500 dark:hover:text-emerald-500;
}

.btn {
    @apply disabled:opacity-60 border-2 leading-none px-2 py-3 rounded-md bg-transparent hover:bg-slate-100 text-slate-500 hover:text-blue-500 border-slate-200 hover:border-blue-500 dark:bg-transparent dark:hover:bg-slate-900 dark:text-slate-500 dark:hover:text-emerald-500 dark:border-slate-700 dark:hover:border-emerald-400;
}

.entry {
    @apply disabled:opacity-60 w-full focus:outline-none border-2 p-2 border-slate-200 focus:border-blue-500 rounded-md bg-transparent focus:bg-slate-100 focus:text-slate-800 dark:border-slate-700 dark:focus:border-emerald-500 dark:focus:bg-slate-900 dark:focus:text-slate-100;
}

.link {
    @apply text-blue-500 hover:text-blue-400 dark:text-emerald-500 dark:hover:text-emerald-400;
}

.article {
    @apply grid grid-rows-[1fr_auto] rounded-md w-min min-w-[320px] h-fit overflow-hidden bg-slate-50 dark:bg-slate-800 drop-shadow-md;
}

* {
    scrollbar-width: thin;
    scrollbar-color: hsla(0, 0%, 0%, 48%) hsla(0, 0%, 0%, 18%);
}

::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background-color: hsla(0, 0%, 0%, 18%);
}

::-webkit-scrollbar-thumb {
    background-color: hsla(0, 0%, 0%, 48%);
}

::-webkit-scrollbar-corner {
    background-color: hsla(0, 0%, 0%, 48%);
}

body {
    margin: 0;

    font-family: Nunito, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    min-block-size: 100vh;
}

svg {
    fill: currentColor !important;
    inline-size: 18px !important;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
}

button {
    text-transform: uppercase;
    cursor: pointer;
}

:disabled {
    pointer-events: none;
    user-select: none;
}

article img,
article video {
    max-inline-size: 100%;
}
